import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { StaticQuery, graphql } from 'gatsby';

import '../assets/sass/new-age.scss';

class Layout extends Component {
  render() {
    const { children } = this.props;
    return (
      <StaticQuery
        query={graphql`
          query SiteTitleQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={data => (
          <>
            <Helmet
              title={data.site.siteMetadata.title}
              meta={[
                { name: '100% Sri Lankan Coconut fiber and cocopeat products and 100% Sri Lankan Natural Spices', content: "Sri Lanka's No:01 Spices and coconut product exporter" },
                { name: 'Twisted fiber Coir twine Husk chips Fiber pith Grow Bags Mix Fiber paper Sri Lankan Natural Spices', content: '100% Sri Lankan Coconut fiber and cocopeat products and 100% Sri Lankan Natural Spices' },
              ]}
            >
              <meta charSet="utf-8" />
              <title>Nuwan Export: Sri Lanka's No:01 Spices and coconut product exporter</title>
              <meta name="description" content="100% Sri Lankan Coconut fiber and cocopeat products and 100% Sri Lankan Natural Spices"/>
              <link rel="canonical" href="https://plantmatecoir.com/" />
            </Helmet>
            <div className={'page-top'}>{children}</div>
          </>
        )}
      />
    );
  }
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  noFooter: PropTypes.bool,
  noHeader: PropTypes.bool,
  noSiteHeader: PropTypes.bool,
  activeLink: PropTypes.string,
};

export default Layout;
